import React, { Component } from 'react'


import auth from '../services/authService'
import Button from './ui/elements';


export default class loginForm extends Component {
  state = {
    submitProcessing: false,
    errorMessage: ''
  }

  username = React.createRef();
  password = React.createRef();

  normalState = "Login";
  processingState = (
    <React.Fragment>
      <span className="spinner-border spinner-border-sm"></span>
      <span> Loading</span>
    </React.Fragment>
  );




  handleSubmit = e => {
    e.preventDefault();
    this.setState({ submitProcessing: true });

    if (this.username.current.value == "" || this.password.current.value == "") {
      this.setState({
        errorMessage: "Username and password is required to be filled.",
        submitProcessing: false
      })
    } else {
      this.doSubmit(this.username.current.value, this.password.current.value);
    }

  }

  doSubmit = async (username, password) => {
    const { data: result } = await auth.login(username, password);
    if (result.status == 'success') {
      localStorage.setItem("customerToken", result.token);
      this.props.history.push(this.props.location.state.previousPage);
    } else {
      this.setState({
        errorMessage: result.message,
        submitProcessing: false
      });
      this.password.current.value = '';
      this.password.current.focus();
    }
  }



  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="col-md-4 mt-4 mx-auto">
            <div className="text-center mb-2">
              <img src="/assets/images/logo.png" height="50" />
            </div>
            <div className="card">
              <div className="card-header">Customer Login</div>
              <div className="card-body">
                <div id="message">
                  {this.state.errorMessage && (<div className="alert alert-danger">{this.state.errorMessage}</div>)}
                </div>
                <form onSubmit={this.handleSubmit}>
                  <div className="form-group">
                    <label htmlFor="username">
                      Username <span className="text-danger">*</span>
                    </label>
                    <input type="text" id="username" className="form-control" ref={this.username} />
                  </div>
                  <div className="form-group">
                    <label htmlFor="password">
                      Password <span className="text-danger">*</span>
                    </label>
                    <input
                      type="password"
                      id="password"
                      className="form-control"
                      ref={this.password}
                    />
                  </div>
                  <div>
                    <button disabled={this.state.submitProcessing} className="btn btn-block btn-primary">{this.state.submitProcessing ? this.processingState : this.normalState}</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
