import React, { Component } from 'react'
import { Redirect, Route } from 'react-router-dom'

import { Header, Footer } from '../components/common';
import auth from '../services/authService';


export default class ProtectedRoute extends Component {


  render() {

    const { component: Component, render, ...rest } = this.props;

    if (localStorage.getItem("customerToken") && auth.getCurrentUser()) {

      if (this.props.hideTemplate != true) {
        return (
          <Route {...rest}>
            <Header />
            <div className="my-3 my-md-5">
              <div className="container">
                {this.props.children}
              </div>
            </div>
            <Footer />
          </Route>
        );
      } else {
        return (<Route {...rest}>{this.props.children}</Route>);
      }


      return (<Route {...rest}>
        {this.props.hideTemplate != true ? (<Header />) : ''}
        {this.props.children}
        {this.props.hideTemplate != true ? (<Footer />) : ''}
      </Route>)
    } else {

      var nextLocation = {
        pathname: '/login',
        state: { previousPage: this.props.location }
      }

      return (<Redirect to={nextLocation} />);
    }

  }


}


